<template>
    <div>
        <div class="title" style="font-size: 23px;padding-left: 20px; margin-bottom: 20px;float:left">
              <span class="el-icon-ship"></span>
              
        </div>
        <br>
        <el-card style="margin-top:50px">
             <el-row>
                 <el-col :span="3">
                      <el-select v-model="type" placeholder="">
                          <el-option
                           v-for="item in types"
                           :key="item.value"
                           :label="item.label"
                           :value="item.value">
                           </el-option>
                     </el-select>
                 </el-col>
                 <el-col :span="4" :offset="1">
                     <el-input></el-input>
                 </el-col>
                 <el-col :span="2" :offset="5">
                      <el-button type="primary" style="width:150px"></el-button>
                 </el-col>
             </el-row>
             <el-table ref="multipleTable" @current-change="clickChange" :data="shipTeamList.slice((currentPage-1)*pageSize,currentPage*pageSize)" tooltip-effect="dark" style="width: 100%">
<!--                   <el-table-column  type="selection" width="55"></el-table-column>-->
               <el-table-column label="" width="55">
                 //
                 <template slot-scope="scope">
                   <el-radio  v-model="tableRadio" :label="scope.row"><i></i></el-radio>
                 </template>
               </el-table-column>
                   <el-table-column  prop="TEAMID" label=""  ></el-table-column>
                   <el-table-column  prop="TEAMNAME" label=""  ></el-table-column>
                   <el-table-column  prop="SHIPNAME" label=""  ></el-table-column>
                   <el-table-column  prop="TEAMLEADERNAME" label=""  ></el-table-column>
                   <el-table-column  prop="TEAMLEADERPHONE" label=""  ></el-table-column>
                   <el-table-column  prop="SAFEDISTANCE" label=""  ></el-table-column>
                   <el-table-column  prop="SHIPCOUNT" label=""  ></el-table-column>
                   <el-table-column  prop="WORKSHIPCOUNT" label=""  ></el-table-column>
             </el-table>
             <el-pagination
                small
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[10, 20, 30, 50, 100]"
                :page-size="pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="shipTeamList.length">
             </el-pagination>
                <div style="margin-top:20px">
                    <el-button type="primary" size="mini" @click="queryShipTeamShipList"></el-button>
                    <el-button type="primary" size="mini" @click="queryShipTeamAlarmList"></el-button>
                    <el-button type="primary" size="mini"></el-button>
                </div>
        </el-card>
          <el-dialog title="" :visible.sync="dialogFormVisible">
            <div>
              <el-table ref="multipleTable" :data="shipList" tooltip-effect="dark" style="width: 100%">
                <el-table-column  type="index" label=""  ></el-table-column>
                <el-table-column  prop="shipId" label="ID"  ></el-table-column>
                <el-table-column  prop="workStatus" label=""  ></el-table-column>
              </el-table>
            </div>
          </el-dialog>
      <el-dialog title="" :visible.sync="dialogFormVisible2">
        <div>
          <el-table ref="multipleTable" :data="alarmList" tooltip-effect="dark" style="width: 100%">
            <el-table-column  type="index" label=""  ></el-table-column>
            <el-table-column  prop="alarmTime" label=""  ></el-table-column>
            <el-table-column  prop="shipId" label="ID"  ></el-table-column>
            <el-table-column  prop="longitude" label=""  ></el-table-column>
            <el-table-column  prop="latitude" label=""  ></el-table-column>
            <el-table-column  prop="referenceShipId" label="ID"  ></el-table-column>
            <el-table-column  prop="referenceLongitude" label=""  ></el-table-column>
            <el-table-column  prop="referenceLatitude" label=""  ></el-table-column>
            <el-table-column  prop="safeDistance" label=""  ></el-table-column>
            <el-table-column  prop="realDistance" label=""  ></el-table-column>
          </el-table>
        </div>
      </el-dialog>
    </div>
</template>
<script>
  import axiosPost from "../../js/utils/axiosPost";
export default {
    data()
    {
        return{
            type:'',
            time:'',
            types:[
                {
                    value:'1',
                    label:''
                },
                {
                    value:'2',
                    label:'ID'
                },
                {
                    value:"3",
                    label:'MMIS'
                },
                {
                    value:'4',
                    label:''
                },
                {
                     value:'5',
                     label:'ID'

                }
            ],
            // 
            currentPage: 1,
            // 
            pageSize: 10,
            shipTeamList : [],
            shipList:[],
            alarmList:[],
            teamId:'',
            //
            tableRadio:'',
            dialogFormVisible:false,
            dialogFormVisible2:false
        }
    },
    methods:{
        //
        clickChange (item) {
            this.tableRadio = item;
            console.log(this.tableRadio)
        },
        // 
        handleSizeChange(val) {
            this.pageSize = val;
            this.currentPage = 1;
        },
        // 
        handleCurrentChange(val) {
            this.currentPage = val;
        },
        //
        queryShipTeamShipList(){
            if(this.tableRadio==''){
                this.$alert('');
            }
            else {
                this.teamId = this.tableRadio.TEAMID;
                let args = '{"userId":"' + localStorage.getItem("userEncryptId") + '","teamId":"' + this.teamId + '" }'
                axiosPost("http://115.231.112.237:8088/CTBT/services/Ships", "queryShipTeamShipList", args, this).then((res) => {
                    let jsonObj=JSON.parse(res);
                    this.shipList = jsonObj;
                })
                this.dialogFormVisible=true;
            }
        },
        //
        queryShipTeamAlarmList(){
            if(this.tableRadio==''){
                this.$alert('');
            }
            else {
                this.teamId = this.tableRadio.TEAMID;
                let args = '{"userId":"' + localStorage.getItem("userEncryptId") + '","teamId":"' + this.teamId + '" }'
                axiosPost("http://115.231.112.237:8088/CTBT/services/Ships", "queryShipTeamAlarmList", args, this).then((res) => {
                    let jsonObj=JSON.parse(res);
                    this.alarmList=jsonObj;
                })
                this.dialogFormVisible2=true;
            }
        },
        //
        queryShipTeamAlarmDetailList(){
            if(this.tableRadio==''){
                this.$alert('');
            }
            else {
                this.teamId = this.tableRadio.TEAMID;
                let args = '{"userId":"' + localStorage.getItem("userEncryptId") + '","teamId":"' + this.teamId + '" }'
                axiosPost("http://115.231.112.237:8088/CTBT/services/Ships", "queryShipTeamAlarmList", args, this).then((res) => {
                    let jsonObj=JSON.parse(res);
                    this.alarmList=jsonObj;
                })
                this.dialogFormVisible2=true;
            }
        }
    },
    mounted() {
        //
        let args = '{"userId":"'+ localStorage.getItem("userEncryptId")  +'","page":"1","pageSize":"100" }'
        axiosPost("http://115.231.112.237:8088/CTBT/services/Ships","queryShipTeamList",args,this).then((res) => {
            let jsonObj=JSON.parse(res);
            let data=jsonObj.data;
            this.shipTeamList=data.list;
        })
    }
}
</script>



