<template>
    <div>
        <div class="title" style="font-size: 23px;padding-left: 20px; margin-bottom: 20px;float:left">
              <span class="el-icon-phone-outline"></span>
              
        </div>
        <br>
        <el-card style="margin-top:50px">
             <el-row>
                 <el-col :span="3">
                        <el-date-picker
                           v-model="time"
                           type="daterange"
                           range-separator=""
                           start-placeholder=""
                           end-placeholder="">
                       </el-date-picker>
                 </el-col>
                 <el-col :span="2" :offset="5">
                      <el-button type="primary" style="width:150px" ></el-button>
                 </el-col>
                 <el-col :span="5" :offset="4">
                     <el-input placeholder=""></el-input>
                 </el-col>
                 <el-col :span="3" :offset="1"><el-button type="primary" style="width:150px"></el-button></el-col>
             </el-row>
               <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
                   <el-table-column  type="index" width="50" label=""></el-table-column>
                   <el-table-column  prop="id" label=""  width="120"></el-table-column>
                   <el-table-column  prop="number" label=""  width="120"></el-table-column>
                   <el-table-column  prop="name" label=""  width="120"></el-table-column>
                   <el-table-column  prop="human" label=""  width="120"></el-table-column>
                   <el-table-column  prop="mmsi" label=""  width="120"></el-table-column>
                   <el-table-column  prop="text" label=""  width="120"></el-table-column>
                   <el-table-column  prop="long" label=""  width="120"></el-table-column>
                   <el-table-column  prop="lat" label=""  width="120"></el-table-column>
                   <el-table-column  prop="mmsi" label=""  width="120"></el-table-column>
                   <el-table-column  prop="status" label=""  width="120"></el-table-column> 
               </el-table>
                <div style="margin-top:20px">
                    <el-col :span="2"><el-button type="primary" size="mini"></el-button></el-col>
                    <el-col :span="2"><el-button type="primary" size="mini"></el-button></el-col>
                    <el-col :span="2"><el-button type="primary" size="mini"></el-button></el-col>
                    <el-col :span="10">
                        <el-pagination
                            background
                            layout="prev, pager, next, jumper"
                            :total="1000">
                        </el-pagination>
                    </el-col>
                </div>
        </el-card>
    </div>
</template>
<script>
export default {
    data()
    {
        return{
            type:'1',
            time:'',
            types:[
                {
                    value:'1',
                    label:''
                },
                {
                    value:'2',
                    label:'ID'
                },
                {
                    value:"3",
                    label:'MMIS'
                },
                {
                    value:'4',
                    label:''
                },
                {
                     value:'5',
                     label:'ID'

                }
            ],
             tableData: [],

        }
    }
}
</script>


